<template>
  <base-material-card
    color="success"
    :icon="icon"
    inline
    :title="title"
    class="px-5 py-3"
  >
    <v-row>
      <v-col>
        <v-data-table
          v-model="selectedItems"
          :headers="columns"
          :items="items"
          :page.sync="page"
          :items-per-page.sync="itemsPerPage"
          hide-default-footer
          :show-select="select"
          :item-key="key"
          :search="search"
          @page-count="pageCount = $event"
        >
          <!-- Top con selector de registros por página. filtros de busquedas y barra de herramientas -->
          <template v-slot:top>
            <v-row
              align="center"
              justify="left"
              no-gutter
            >
              <v-toolbar
                dense
                flat
              >
                <v-toolbar-items>
                  <!-- Boton de agregar -->
                  <v-btn
                    v-if="actions.includes('insert')"
                  >
                    <v-icon>
                      mdi-plus
                    </v-icon>
                    Nuevo
                  </v-btn>
                  <!-- Boton de Edicion masiva -->
                  <v-btn>
                    <v-icon>
                      mdi-pencil
                    </v-icon>
                    Editar masivo
                  </v-btn>
                </v-toolbar-items>
                <v-spacer />
                <v-toolbar-items>
                  <!-- Boton de filtros -->
                  <v-btn>
                    <v-icon>
                      mdi-filter
                    </v-icon>
                    Filtros
                  </v-btn>
                </v-toolbar-items>
              </v-toolbar>
            </v-row>
            <v-row
              align="center"
              justify="center"
              no-gutters
            >
              <!-- Select de items por página -->
              <v-col
                class="mx-4"
                cols="auto"
              >
                <v-select
                  v-model="itemsPerPage"
                  :items="[2,5,10,15,20,25,50]"
                  label=""
                  flat
                  prefix="Mostrar"
                  suffix="registros"
                />
              </v-col>
              <v-spacer />
              <!-- Texto de filtros -->
              <v-col
                class="mx-4"
                cols="4"
              >
                <v-text-field
                  v-model="search"
                  prepend-icon="mdi-magnify"
                  label="Busqueda"
                  single-line
                  hide-details
                />
              </v-col>
            </v-row>
          </template>
          <!-- Checkbox de seleccion de encabezado -->
          <template
            v-if="select"
            v-slot:header.data-table-select="{ on, props }"
          >
            <v-simple-checkbox
              color="purple"
              v-bind="props"
              v-on="on"
            />
          </template>
          <!-- Items -->
          <template v-slot:body="{ item }">
            <tbody>
              <tr
                v-for="item in items"
                :key="item"
              >
                <td>
                  <v-col>
                    <v-checkbox
                      v-model="selectedItems"
                      :value="item"
                    />
                  </v-col>
                </td>
                <td
                  v-for="column in columns"
                  :key="column"
                >
                  <!-- Campos de tipo imagen -->
                  <v-col
                    v-if="column.type === 'image'"
                  >
                    <img
                      :src="item[column.value]"
                      :alt="column.text"
                    >
                  </v-col>
                  <!-- Campos de tipo texto -->
                  <v-col
                    v-if="column.type === 'text'"
                  >
                    <p v-text="item[column.value]" />
                  </v-col>
                  <!-- Campos de tipo switch, para booleanos editables -->
                  <v-col
                    v-if="column.type === 'switch'"
                  >
                    <v-switch
                      v-model="item[column.value]"
                      @change="checkedItemClick(item.id, column.value, item[column.value])"
                    />
                  </v-col>
                  <!-- Campos de tipo check, para booleanos de solo lectura -->
                  <v-col
                    v-if="column.type === 'bool'"
                  >
                    <v-checkbox
                      v-model="item[column.value]"
                      readonly
                      on-icon="mdi-check-circle"
                      off-icon=""
                    />
                  </v-col>
                  <!-- Botones de accion -->
                  <v-col
                    v-if="column.value === 'action'"
                    align="center"
                  >
                    <!-- Accion de ver -->
                    <v-btn
                      v-if="actions.includes('view')"
                      class="px-2 ml-1"
                      :color="'info'"
                      min-width="0"
                      small
                      @click="viewItem(item.id)"
                    >
                      <v-icon
                        small
                        v-text="'mdi-eye'"
                      />
                    </v-btn>
                    <!-- Accion de editar -->
                    <v-btn
                      v-if="actions.includes('edit')"
                      class="px-2 ml-1"
                      :color="'success'"
                      min-width="0"
                      small
                      @click="editItem(item.id)"
                    >
                      <v-icon
                        small
                        v-text="'mdi-pencil'"
                      />
                    </v-btn>
                    <!-- Accion de eliminar -->
                    <v-btn
                      v-if="actions.includes('delete')"
                      class="px-2 ml-1"
                      :color="'error'"
                      min-width="0"
                      small
                      @click="deleteItem(item.id)"
                    >
                      <v-icon
                        small
                        v-text="'mdi-close'"
                      />
                    </v-btn>
                  </v-col>
                </td>
              </tr>
            </tbody>
          </template>
          <!-- Footer -->
          <template v-slot:footer="footer">
            <v-row
              align="center"
            >
              <v-spacer />
              <!-- Registros mostrados por pagina-->
              <v-col
                cols="auto"
                class="text-end"
              >
                Mostrando registros del {{ footer.props.pagination.pageStart+1 }} hasta el {{ footer.props.pagination.pageStop}}, de un total de {{ total }}.
              </v-col>
              <!-- Paginacion -->
              <v-col cols="auto">
                <v-pagination
                  v-model="page"
                  :length="pageCount"
                  total-visible="7"
                />
              </v-col>
            </v-row>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
  </base-material-card>
</template>

<script>
  export default {
    name: 'Datatable',
    props: {
      title: {
        default: 'Lista',
        type: String,
      },
      actions: {
        default: () => [],
        type: Array,
      },
      icon: {
        default: 'mdi-clipboard-text',
        type: String,
      },
      columns: {
        default: () => [],
        type: Array,
      },
      itemsPerPage: {
        default: 10,
        type: Number,
      },
      select: {
        default: false,
        type: Boolean,
      },
      items: {
        default: () => [],
        type: Array,
      },
      key: {
        default: 'id',
        type: String,
      },
    },
    data () {
      return {
        page: 1,
        pageCount: 1,
        btnItems: false,
        search: '',
        selectedItems: [],
      }
    },
    computed: {
      total: function () {
        return this.items.length
      },
    },
    created () {
      if (this.actions.length > 1) {
        this.columns.push({ value: 'action', text: 'Acciones', sortable: false, width: '20%', align: 'center' })
      }
    },
    methods: {
      // Emite el evento de vista del item, pasando como parámetro el id
      viewItem (itemId) {
        this.$emit('viewItem', itemId)
      },
      // Emite el evento de edicion, pasando como parámetro el id
      editItem (itemId) {
        this.$emit('edit', itemId)
      },
      // Emite el evento de edicion, pasando como parámetro el id
      checkedItemClick (itemId, field, value) {
        this.$emit('checkedItemClick', itemId, field, value)
      },
      // Modal para mostrar la confirmacion de eliminacion del item
      deleteItem (itemId) {

      },
    },
  }
</script>

<style scope>
  .v-select__selection {
    font-family: "Roboto", sans-serif;
    font-size: 16px
  }
  .v-text-field > .v-input__control > .v-input__slot::before {
    border:none;
  }
  .v-select__selections input {
    display: none;
  }
  .v-data-table .col {
    padding-left: 0px;
    padding-right: 0px;
  }
  .v-sheet .v-toolbar__content .theme--light.v-btn:not(.v-btn--flat):not(.v-btn--text):not(.v-btn--outlined) {
    color: black;
  }
  .v-toolbar__content {
    padding-left: 0px;
    padding-right: 0px;
  }
  .v-toolbar__items {
    height: auto;
  }
</style>
